import "core-js/modules/es6.string.iterator";
import "core-js/modules/es6.array.from";

/**
 * Implement Gatsby's Browser APIs in this file.
 *
 * See: https://www.gatsbyjs.org/docs/browser-apis/
 * **These should be gatsby plugins, but some weren't working.**
 * **For now I'm monkey-patching them...**
 */
import elasticScroll from 'elastic-scroll-polyfill';
import { toKebabCase } from './src/utils';

function addDataLabelToTdElements() {
  var labels = Array.from(document.querySelectorAll('th')).map(function (th) {
    return th.textContent;
  });
  var rowTds = Array.from(document.querySelectorAll('tbody tr')).map(function (tr) {
    return Array.from(tr.querySelectorAll('td'));
  });
  rowTds.forEach(function (tds) {
    tds.forEach(function (td, index) {
      td.setAttribute('data-label', labels[index]);
    });
  });
}

function addElasticScrollingToCodeBlocks() {
  if (/Mac/.test(navigator.platform)) {
    elasticScroll({
      targets: 'pre[class*="language"]'
    });
  }
}

function autoLinkHeaders() {
  var headers = Array.from(document.querySelectorAll('h3,h4,h5,h6'));
  headers.forEach(function (header) {
    var a = document.createElement('a');
    var href = toKebabCase(header.textContent);
    a.id = href;
    a.href = "#" + href;
    a.innerHTML = header.innerHTML;
    header.innerHTML = '';
    header.appendChild(a);
  });
}

export function onRouteUpdate() {
  addDataLabelToTdElements();
  addElasticScrollingToCodeBlocks();
  autoLinkHeaders();
}